<template>
  <div class="article">
    <HomeHeader
      @toHome="logoCilck"
      :backgroundColor="'#8893a7'"
      :value="22"
    ></HomeHeader>
    <div class="content">
      <div class="box">
        <div class="title">{{ info.title }}</div>
        <div class="info">
          <div class="author">{{ info.author }}</div>
          <div class="time">于 {{ info.createTime }} 发布</div>
        </div>
        <Markdown :markdown="markdown" ref="markdownInfo"></Markdown>
      </div>
    </div>
  </div>
</template>

<script>
import mdArr from "@/assets/article";
import appConfig from '@/config/app.json'

export default {
  name: "Article",

  data() {
    return {
      id: null,
      markdown: "",
      info: {},
      Author: appConfig.Author,
    };
  },

  mounted() {
    this.id = this.$route.query.id;
    this.markdown = mdArr.filter((e) => e.title === this.id)[0].html;
    window.scrollTo(0, 0);
    setTimeout(() => {
      this.info = this.$refs.markdownInfo.info;
      document.title = `${this.info.title} | ${this.Author}`;
    }, 1);
  },

  methods: {
    logoCilck() {
      sessionStorage.setItem("headBg", "transparent");
      this.$router.push("/home");
    },
  },
};
</script>

<style lang="less" scoped>
.article {
  .content {
    display: flex;
    justify-content: center;
    background-color: #1f262f;
    .box {
      box-sizing: border-box;
      margin-top: 20px;
      padding: 10px 20px;
      width: 1200px;
      min-height: calc(100vh - 60px - 20px);
      background-color: #f1f1f1;
      .title {
        padding: 20px 0;
        font-size: 28px;
        font-weight: 600;
      }
      .info {
        display: flex;
        align-items: center;
        margin-bottom: 20px;
        padding: 10px 15px;
        background-color: #b8b8b82f;
        border-radius: 4px;
        font-size: 14px;
        color: #333;
        .author {
          margin-right: 10px;
          color: #000;
          font-family: "font-a";
          font-size: 20px;
        }
        .time {
          font-size: 14px;
        }
      }
    }
  }
}
</style>
